<template>
  <div>
    <main-navigation-bar></main-navigation-bar>

    <div class="table-title">신규가입회원</div>
    <div class="subcontents-define">총 회원수 {{this.getCustomer.rows.length}} 명
      <div class="add-button-container"><button class="crud-button">회원 추가 등록</button></div>
    </div>
    <vue-good-table
      :columns="getCustomer.columns"
      :rows="getCustomer.rows"
      :sort-options="{
        enabled: true,
        initialSortBy: {field: 'id', type: 'number'}
      }"
      :select-options="{
        enabled: true,
        clearSelectionText: '선택 해제'
        }"
      :search-options="{ enabled: true }"
      :pagination-options="{
        enabled: true,
        mode: 'pages',
        perPage: 5,
        perPageDropdown: [5, 10, 15, 20]
      }"
      @on-row-click="nop"
      @on-selected-rows-change="selectionChanged">
      <div slot="selected-row-actions">
        <button class="crud-button in-table-button">회원 정보 수정</button>
        <button class="crud-button in-table-button">회원 정보 삭제</button>
      </div>
    </vue-good-table>
    
    <div class="table-title">최근등록게시물</div>
    <div class="subcontents-define">총 게시물 수 {{this.getBoard.rows.length}} 개
      <div class="add-button-container"><button class="crud-button">새 글 작성</button></div>
    </div>
    <vue-good-table
      :columns="getBoard.columns"
      :rows="getBoard.rows"
      :sort-options="{
        enabled: true,
        initialSortBy: {field: 'id', type: 'number'}
      }"
      :select-options="{
        enabled: true,
        clearSelectionText: '선택 해제'
      }"
      :search-options="{ enabled: true }"
      :pagination-options="{
        enabled: true,
        mode: 'pages',
        perPage: 5,
        perPageDropdown: [5,10,15,20]
